import React, { Component } from "react";
import posed from "react-pose";

const El = posed.div({
  enter: {
    y: 0,
    opacity: 1
  },
  exit: {
    y: -50,
    opacity: 0
  }
});

const PoseContainer = posed.div({
  enter: {
    staggerChildren: 50
  }
});

export default class Services extends Component {
  render() {
    const { services } = this.props.data.lang
    const ServiceItem = services.map((service, i) => (
      <div className="col-md-4 col-sm-6" key={i}>
        <div className={"service-block id" + i}>
          <div className="service-info">
            <div className="service-image">
              <span
                className={"fa " + service.faIcon}
              />
              {/* {i === 5 && <span className="x-fb">x</span>} */}
            </div>
          </div>
          <h5>{service.name}</h5>
          <p>{service.desc}</p>
        </div>
      </div>
    ));
    return (
      <PoseContainer>
        <div id="services">
          <El>
            <div className="header">
              <h1>{this.props.data.lang.menus[2]}</h1>
            </div>
          </El>
          <El>
            <div className="content">
              <div className="row">
                {/* <div className="col-sm-12">
                  <div className="block-title">
                    <h3>{this.props.data.lang.servSubtitle}</h3>
                  </div>
                </div> */}
                {ServiceItem}
              </div>
            </div>
          </El>
        </div>
      </PoseContainer>
    );
  }
}
