import React, { Component } from "react";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from "reactstrap";
import { NavLink } from "react-router-dom";
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

export default class NavBar extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.collapse = this.collapse.bind(this);
    this.cs = this.cs.bind(this);
    this.ConvertLangAndCollapse = this.ConvertLangAndCollapse.bind(this);

    this.state = {
      isOpen: false,
    };
  }

  componentDidMount() {
    window.addEventListener("scroll", this.changeNav);
  }

  changeNav() {
    const distanceY = window.pageXOffset || document.documentElement.scrollTop;
    const shrinkOn = 40;
    const nav = document.getElementById("navbar");
    if (nav) {
      distanceY > shrinkOn
        ? nav.classList.add("isScrollDown")
        : nav.classList.remove("isScrollDown");
    }
  }

  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

  collapse() {
    this.setState({
      isOpen: false
    });
  }

  ConvertLangAndCollapse(lang) {
    this.setState({
      isOpen: false
    });
    this.props.convertLang(lang);
  }

  cs() {
    const MySwal = withReactContent(Swal)
    MySwal.fire({
      title: 'Not Available Yet',
      text: 'Sorry for this issue!',
      type: 'warning',
      confirmButtonText: '<i class="fa fa-thumbs-up"></i> Great!'
    })
  }

  render() {
    const { first, middle, last } = this.props.data.name;
    return (
      <Navbar className="fixed-top" color="light" light expand="md" id="navbar">
        <NavbarBrand id="nameBrand">
          {first}{middle ? ' ' + middle : ''}{last ? ' ' + last : ''}
        </NavbarBrand>
        <NavbarToggler onClick={this.toggle} />
        <Collapse isOpen={this.state.isOpen} navbar>
          <Nav className="ml-auto" navbar>
            <NavItem>
              <NavLink
                exact
                to={process.env.PUBLIC_URL + "/"}
                activeClassName="active"
                className="nav-link"
                onClick={this.collapse}
              >
                {this.props.data.lang.menus[0]}
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                to={process.env.PUBLIC_URL + "/experiences"}
                activeClassName="active"
                className="nav-link"
                onClick={this.collapse}
              >
                {this.props.data.lang.menus[1]}
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                to={process.env.PUBLIC_URL + "/services"}
                activeClassName="active"
                className="nav-link"
                onClick={this.collapse}
              >
                {this.props.data.lang.menus[2]}
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                to={process.env.PUBLIC_URL + "/portfolios"}
                activeClassName="active"
                className="nav-link"
                onClick={this.collapse}
              >
                {this.props.data.lang.menus[3]}
              </NavLink>
            </NavItem>

            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle nav caret title="Select your language">
                {this.props.data.lang.menus[4]}
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem
                  onClick={() => this.ConvertLangAndCollapse("english")}
                >
                  English
                </DropdownItem>
                <DropdownItem
                  onClick={() => this.ConvertLangAndCollapse("indo")}
                >
                  Bahasa Indonesia
                </DropdownItem>
                <DropdownItem
                  onClick={() => this.ConvertLangAndCollapse("sasak")}
                >
                  Sasak (Lombok)
                </DropdownItem>
                <DropdownItem onClick={this.cs}>العربية</DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
          </Nav>
        </Collapse>
      </Navbar>
    );
  }
}
