import React, { Component } from "react";
import posed from "react-pose";

const El = posed.div({
  enter: {
    y: 0,
    opacity: 1
  },
  exit: {
    y: -50,
    opacity: 0
  }
});

const Container = posed.div({
  enter: {
    staggerChildren: 50
  }
});

const Pf = (props) => {
  return (
    <div className="col-sm-6 col-md-4 mt-2">
      <div className="card">
        {/* <img className="card-img-top bg-dark" alt={props.alt} height='250px'/> */}
        <span className='fa fa-globe' style={{fontSize: '250px',color: '#8fc63c', backgroundColor: '#1d5078'}}></span>
        <div className="card-img-overlay">
          <div title="Tech used" className="tech rounded py-3 mt-4">
            <div>{props.tech}</div>
          </div>
          <a href={props.linkDemo}>
            <div
              title={(props.linkDemo !== '#') ? 'Click me' : 'Not Available'}
              className="demo rounded py-3"
            >
              <div>DEMO</div>
            </div>
          </a>
          <a href={props.linkDownload}>
            <div title={props.title} className="download rounded py-3">
              <div><i className="fa fa-heart" aria-hidden="true"></i> {(props.name === 'rankingForki') ? 'Sold out' : 'Download'}</div>
            </div>
          </a>
        </div>
        <div className="card-header" style={{background: '#8fc63c', color: '#2c638d'}}>
          <h6 className="card-title font-weight-bold">{props.title}</h6>
        </div>
      </div>
    </div>
  );
}

export default class Portfolios extends Component {
  render() {
    const { portfolios, menus } = this.props.data.lang
    const Items = portfolios.items.map((item, key) => (
      <Pf
        key={key}
        tech={item.tech}
        linkDemo={item.linkDemo}
        linkDownload={item.linkDownload}
        // src={require('../assets/images/pf/' + item.imgName)}
        alt={item.title}
        title={item.title}
        name={item.name}
      />
    ));

    return (
      <Container>
        <div id="portfolios">
          <El>
            <div className="header">
              <h1>{menus[3]}</h1>
            </div>
          </El>
          <El>
            <div className="content">
              <div className="row">
                {Items}
              </div>
              {/* <div className="alert alert-warning mt-2">
                <span>
                  Note : {portfolios.note}
                </span>
              </div> */}
            </div>
          </El>
        </div>
      </Container>
    );
  }
}
