import React, { Fragment } from 'react';
import { Switch, Route } from "react-router-dom";
import posed, { PoseGroup } from "react-pose";
import { Navbar, Footer } from "./layout";
import Home from "./Home";
import Experiences from "./Experiences";
import Services from "./Services";
import Portfolios from "./Portfolios";
import P404 from "./404";

export default function Main(props) {
  const { rootData, location, convertLang } = props;
  const PoseEl = posed.div({
    enter: {
      opacity: 1,
      beforeChildren: true
    },
    exit: {
      opacity: 0
    }
  });

  document.body.style.background = '#8fc63c';
  return (
    <Fragment>
      <Navbar data={rootData} convertLang={convertLang} />
      <div id="box">
        <PoseGroup>
          <PoseEl key={location.pathname}>
            <Switch>
              <Route
                exact path="/"
                render={props =>
                  <Home data={rootData} {...props} />
                }
                key="home"
              />
              <Route
                exact path="/experiences"
                render={props =>
                  <Experiences data={rootData} {...props} />
                }
                key="exp"
              />
              <Route
                exact path="/services"
                render={props =>
                  <Services data={rootData} {...props} />
                }
                key="services"
              />
              <Route
                exact path="/portfolios"
                render={props =>
                  <Portfolios data={rootData} {...props} />
                }
                key="portfolios"
              />
              <Route component={P404} />
            </Switch>
          </PoseEl>
        </PoseGroup>
      </div>
      <Footer data={rootData} />
    </Fragment>
  )
}