import React, { Component } from "react";
import html2canvas from "html2canvas";
import * as jsPDF from "jspdf";
import posed from "react-pose";

export default class Experiences extends Component {
  constructor(props) {
    super(props);

    this.state = {
      swalShow: false
    };
  }

  genCanvas() {
    html2canvas(document.getElementById("exp")).then(function (canvas) {
      const imgData = canvas.toDataURL("image/png");
      const pdf = new jsPDF();
      pdf.addImage(imgData, "JPEG", 0, 0, 210, 260);
      pdf.setFontSize(12);
      pdf.setTextColor(150);
      pdf.text(
        "phone: 081907456710 || email : arfanihidayat@gmail.com",
        50,
        293
      );
      pdf.save("rfunExp.pdf");
    });
  }

  render() {
    const PoseContainer = posed.div({
      enter: {
        staggerChildren: 50
      }
    });

    const El = posed.div({
      enter: {
        y: 0,
        opacity: 1
      },
      exit: {
        y: -50,
        opacity: 0
      }
    });

    const { itemWorks, itemEdu } = this.props.data.lang
    const ItemWork = itemWorks.map((work, i) => (
      <div className="timeline-item" key={i}>
        <h5 className="title">{work.title}</h5>
        <span className="period">{work.period}</span>
        <span className="company">{work.company}</span>
      </div>
    ));

    const ItemEdu = itemEdu.map((edu, i) => (
      <div className="timeline-item" key={i}>
        <h5 className="title">{edu.title}</h5>
        <span className="period">{edu.period}</span>
        <span className="school">{edu.school}</span>
      </div>
    ));

    return (
      <PoseContainer>
        <div id="exp">
          <El>
            <div
              className="header"
              style={{
                backgroundColor: "#343a40"
              }}
            >
              <h1>{this.props.data.lang.menus[1]}</h1>
            </div>
          </El>
          <El>
            <div className="content">
              <div className="row">
                <div className="col-md-6">
                  <div className="block work">
                    <div className="block-title">
                      <h3>{this.props.data.lang.subtitleWork}</h3>
                    </div>
                    <div className="timeline">{ItemWork}</div>
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="block edu">
                    <div className="block-title">
                      <h3>{this.props.data.lang.subtitleEdu}</h3>
                    </div>
                    <div className="timeline">{ItemEdu}</div>
                  </div>
                </div>
              </div>

              {/* <button
                className="btn btn-warning"
                onClick={this.genCanvas}
                data-html2canvas-ignore="data-html2canvas-ignore"
              >
                Download Resume
              </button> */}
            </div>
          </El>
        </div>
      </PoseContainer>
    );
  }
}
